/* shop */
.shop-catalog {
    padding-top: 90px;
    padding-bottom: 110px;
}
.woocommerce-result-count {
    color: #7e7e7e;
    margin: 14px 0 0 0;
    float: left;
}
.woocommerce-ordering, .woocommerce-ordering {
    float: right;
}
.woocommerce-ordering {
    margin: 0 0 1em;
    margin-bottom: 30px;
}
.woocommerce-ordering .orderby {
    -webkit-appearance: none;
    appearance: none;
    color: #7e7e7e;
    height: 50px;
    line-height: 50px;
    outline: none;
    border: none;
    padding: 0 20px;
    background-image: linear-gradient(45deg,transparent 50%,#c3c7c9 50%),linear-gradient(135deg,#c3c7c9 50%,transparent 50%);
    background-position: calc(100% - 24px) calc(1em + 6px),calc(100% - 19px) calc(1em + 6px),100% 0;
    background-size: 5px 5px,5px 5px,2.5em 2.5em;
    background-repeat: no-repeat;
}
.product {
    clear: both;
}
.product-item {
    position: relative;
    font-family: "Montserrat",sans-serif;
    text-align: center;
    margin-bottom: 35px;
}
.product-item .product-media {
    position: relative;
    overflow: hidden;
    background: #f8f8f8;
}
.product-item .wrapper-add-to-cart {
    visibility: hidden;
    opacity: 0;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 10px;
    z-index: 2;
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
}
.product-item:hover .wrapper-add-to-cart {
    visibility: visible;
    opacity: 1;
}
.product-item .wrapper-add-to-cart a {
    display: block;
    margin-top: 0;
    background-color: #7141b1;
    padding: 16px 23px;
    border-radius: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
    transition: all 0.3s ease-in-out;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
}
.product-item:hover a {
    -webkit-transform: translateY(0);
    transform: translateY(0);
}
.product-item .product-media .add-to-cart-inner a:hover {
    background-color: #43baff;
}
.product-item .woocommerce-loop-product__title {
    font-size: 20px;
    line-height: 28px;
    color: #1a1b1e;
    font-weight: 700;
    margin-top: 20px;
    margin-bottom: 3px;
    padding: 0;
}
.product-item .woocommerce-loop-product__title a {
    color: #1a1b1e;
}
.product-item .star-rating {
    overflow: hidden;
    position: relative;
    line-height: 1;
    height: 14px;
    font-size: 14px;
    color: #ffbe4e;
    margin-top: 10px;
    margin-bottom: 10px;
}
.price-product {
    font-size: 16px;
    line-height: 28px;
    display: inline-block;
    color: #43baff;
    font-weight: 600;
}
.product-item span.onsale {
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: .5px;
    background-color: #fd3049;
    padding: 9px 10px;
    line-height: 1;
    position: absolute;
    border-radius: 0;
    top: 0;
    left: 0;
    right: auto;
    min-height: auto;
    min-width: auto;
    margin: 0;
    z-index: 1;
}
.price-product del {
    color: #636363;
    margin-right: 5px;
    opacity: .5;
}
.price-product ins {
    background: transparent;
    display: inline-block;
}
.popular-product .thumb {
    float: left;
    margin-right: 20px;
    background: #f0f6ff;
}
.widget-area .widget ul.popular-product > li {
    margin-bottom: 0;
}
.popular-product li {
    padding: 0 0 30px 0;
}
.popular-product .thumb img {
    width: 70px;
    margin-left: 0;
}
.popular-product li h6 {
    margin-top: 5px;
    margin-bottom: 0;
}
.widget-area .widget ul.popular-product li h6 a {
    display: block;
    font-family: "Montserrat",sans-serif;
    font-size: 16px;
    color: #1a1b1e;
    font-weight: 700;
    line-height: 24px;
}
.popular-product .price-product {
